export default {
    name: 'image-choose',
    props: {

        imgList: {
            //接收的图片列表[{src:12.jpg}]
            type: Array,
            default: function() {
                return []
            }
        },
        quality: {
            //质量
            type: Number,
            default: 1
        },
        count: {
            //最多数量
            type: Number,
            default: 5
        },
        changes: {
            //监听变化
            type: Function,
            default: null
        }
    },

    data() {
        return {

        }
    },
    watch: {

        // imgList: {
        //     handler(val, oldval) {
        //         this.$emit("changes", val);
        //         // console.log(val)
        //     },
        //     deep: true
        // }
    },

    methods: {

        chooseImg() { //选择图片
            // console.log('选择图片')
            var count = this.count - this.imgList.length;
            uni.chooseImage({
                count: count,
                success: (res) => {
                    var imgs = res.tempFilePaths || [];
                    var count = imgs.length + this.imgList.length;
                    var imgList = this.imgList;
                    if (count <= this.count) {
                        imgList = this.imgList.concat(res.tempFilePaths);
                        this.$emit("changes", imgList);
                    } else {
                        var len = this.count - this.imgList.length;
                        // console.log(len)
                        for (var i = 0; i < len; i++) {
                            imgList.push(res.tempFilePaths[i])
                        }
                        this.$emit("changes", imgList);
                        uni.showToast({
                            title: '最多只能添加' + this.count + '张图片',
                            icon: 'none'
                        })

                    }


                },
            })
        },
        close(e) {
            var imgList = this.imgList;
            imgList.splice(e, 1);
            this.$emit("changes", imgList);

        },
        setCapital(i, name) {
            var imgList=this.imgList;
            imgList[0]= imgList.splice(i,1,imgList[0])[0];
            this.$emit("changes", imgList);
        },

    }
}
